import { SettingStore } from '../stores';
class Engine {
   // constructor(prop) {}
    ipfsMain    = SettingStore.getState().ipfs.main;
    ipfsQ       = SettingStore.getState().ipfs.Q;
    popupOn = (popup)=> {
        SettingStore.dispatch({type:'popupTrigger', popup:popup});
    }
    popupOff = (popup)=> {
        SettingStore.dispatch({type:'popupTrigger', popup: false});
    }
    loadingOn = ()=> {
        SettingStore.dispatch({type:'addLoading', id: this.id});
    }
    loadingOff = ()=> {
        SettingStore.dispatch({type:'removeLoading', id: this.id});
    }

    ipfsUri = (uri)=>{
        const idx = uri.indexOf(this.ipfsMain);
        return idx === -1 ? uri : uri.replace(this.ipfsMain, `https://${this.ipfsQ[Math.floor(Math.random() * this.ipfsQ.length)]}/ipfs/`)
    }

    parseIpfsUrl = (url)=>{
        const patt = /(.*)\/([^\?]+)\?([^\/]+)$/;
        const m = url.match(patt);
        return !m || m.length !== 4 ? false : { path: m[1], ipfsCode: m[2], jpCode : m[3]};
    }
    callIpfsJsonpContent = (typeCode, orgurl, callback, cacheSeconds)=>{
        const obj = this.parseIpfsUrl(orgurl);
        const url = this.ipfsUri(`${obj.path}/${obj.jpCode}`);
        const key = obj.ipfsCode;
        console.log('obj -->', obj.ipfsCode, `${obj.path}/${obj.jpCode}`, obj);
        if (!window._img) {
            window._img = {};
            window._jsonp = { };
            
            setInterval( ()=>{
                const _jsonp = window._jsonp;
                for (const k in _jsonp) {
                    const foo = _jsonp[k].foo;
                    if (window[`_${typeCode}`][k]) {
                        if (_jsonp[k].data) {
                            for (let d in foo) {
                                console.log('===placed===>');
                                foo[d](_jsonp[k].data) 
                            }
                            _jsonp[k].foo = {};
                        }
                    }
                  
                    if (!Object.keys(foo).length
                        && (!cacheSeconds ||  new Date().getTime() - _jsonp[k].created > (cacheSeconds * 1000))
                    ) { 
                   // if (_jsonp[k].created && !Object.keys(foo).length && new Date().getTime() - _jsonp[k].created > 12000) {
                        delete window[`_${typeCode}`][k];
                        delete _jsonp[k];
                    }
                    console.log('kkk--->', k,  new Date().getTime())
                }
            }, 300);
        }

        if (!window._img[key]) {
            if (!window._jsonp[key]) window._jsonp[key] = { foo:  {}, created: new Date().getTime()};
            window._jsonp[key].foo[new Date().getTime() + Math.random()] = callback;
            window._img[key] =  (data)=> {
                window._jsonp[key].data = data;
                window._jsonp[key].created = new Date().getTime();
            };
            const target = document.getElementsByTagName('script')[0] || document.head;
            const script = document.createElement('script');
            script.src =  this.ipfsUri(url);
            target.parentNode.insertBefore(script, target);
        } else {
            window._jsonp[key].foo[new Date().getTime() + Math.random()] = callback;
        }
    }
    // -- it main used for JSON data retrive with more cache time -- 
    ipfsJPFetch = async (key, uri, callback, cacheLife)=> {
        const cacheTime = !cacheLife ? 30000 : parseInt(cacheLife) * 1000;
        if (!window.jpQ) window.jpQ = {Q:{}, data:{}, fetches:{}};
        const { Q, data, fetches } = window.jpQ;
        if (!window.jpQ.itv) window.jpQ.itv = setInterval(()=>{

            for (const o in fetches) {
              if (data[fetches[o].key]) {
                fetches[o].foo(data[fetches[o].key].data);
                delete fetches[o];
              }
            }
            for (const o in fetches) {
              if (new Date().getTime() - fetches[o].created > fetches[o].cacheLife) {
                delete fetches[o];
              }
            }
          },100);

        for (const o in data) {
            if (new Date().getTime() - data[o].created  >  data[o].cacheLife) {
                delete data[o];
            }
        }
        if (data[key]) {
          callback(data[key].data);
        } else {
          fetches[new Date().getTime() + Math.random()] = {key:key, foo:callback, created:new Date().getTime(), cacheLife:cacheTime};
          if (!Q[key] || !data[key]) {
            Q[key] = (dt)=> data[key] = { data: dt, created: new Date().getTime(), cacheLife: cacheTime };
            const target = document.getElementsByTagName('script')[0] || document.head;
            const script = document.createElement('script');
            script.src = this.ipfsUri(uri);
            target.parentNode.insertBefore(script, target);
          }
        }
      }

    fetchWithTimeout = async (url, options = {}) => {
       
        options.timeout = !options.timeout? 8000 : options.timeout;

        console.log('===fetchWithTimeout>>', options, options.timeout, url);
        const controller = new AbortController();
        const id = setTimeout(() => controller.abort(), options.timeout);
        const response = await fetch(url, {
          ...options,
          signal: controller.signal  
        });
        clearTimeout(id);
        return response;
      }
    // -- it main used for base64 image or contents retrive with lass cache time used with IPFS image together -- 
    ipfsFetch = async (uri, options = {}, callback, cnt) => {
        const me = this;
        let callnt = !cnt ? 1 : (cnt+1);
        options.timeout = !options.timeout? 6000 : options.timeout;
        const nuri = this.ipfsUri(uri);
        this.get(nuri, options, (v)=>{
            if (v && v.status === 'error') {
                console.log('------------------------Error more try', callnt,  nuri);
                if (callnt < 4) me.ipfsFetch(uri, options = {}, callback, callnt);
                else  console.log('-----------------------no more try ', callnt);
            } else {
                console.log('-----------------------successy', callnt, nuri);
                callback(v);
            }
        });
    }

    get = async (uri, options = {}, callback)=>{
        options.timeout = !options.timeout? 3500 : options.timeout;
        try {
            const response = await this.fetchWithTimeout( uri, options);
            if (callback) callback(await response.json());
        } catch (e) {
            if (callback) callback({status:'error', uri, message:e.message});
            console.log({status:'error', uri, message:e.message});
        }
    }

    post = (uri, data, auth, callback)=>{
        //const me = this;
        fetch(uri, {
            method: 'POST',
            data : data,
            herder: {
                        
            }
        }
        ).then(v=>{
            return v.json()
        }).then((data)=> {    
            callback(data);
        }).catch((err) => {
            callback({status:'failure', message:err.message});
        });
    }


    getContent = (uri,  callback)=>{
        // const me = this;
        fetch(uri, {
            method: 'GET',
        }).then(v=>{
            return v.text()
        }).then((data)=> {
            callback(data);
        }).catch((err) => {
            callback('err');
        });
    }
}

export default Engine;